.container{
  display: flex;
  flex-direction: column;
  justify-content:  center;
  align-items: center;
  height: 100vh;
}

.input{
  height: 38px;
  border-radius: 8px;
  max-width: 300px;
  width: 100%;
  margin: 24px 0;
  padding: 5px;
  outline: none;
  border: 1px solid  #b6b6b6;
}

a{
  text-decoration: none;
  color: black;
  border: 1px solid;
  padding: 10px;
  border-radius: 13px;
}